import { h } from 'preact';
import { noop } from '@ch1/utility';
export function InputNumber({ classes, listenKeyUp, onChange, placeholder, useDecimals, value, }) {
    const parse = useDecimals
        ? (thing) => parseFloat(thing + '')
        : (thing) => parseInt(thing + '', 10);
    if (!useDecimals) {
        value = parseInt(value + '', 10);
    }
    const classString = classes ? classes.join(' ') : '';
    const change = (e) => onChange(parse(e.target.value));
    const keyUp = listenKeyUp ? change : noop;
    const pattern = useDecimals ? '[0-9]*(.[0-9]*)' : '[0-9]*';
    return (h("input", { className: classString, onChange: change, onKeyUp: keyUp, pattern: pattern, placeholder: placeholder || '', type: "text", value: value }));
}
