export class TimeSeriesArray extends Array {
    constructor() {
        super();
        this.sortByActive = this.sortByProp.bind(this, 'lastActive');
        this.sortByActivePercent = this.sortByProp.bind(this, 'lastActivePercent');
        this.sortByConfirmed = this.sortByProp.bind(this, 'lastConfirmed');
        this.sortByNewConfirmed = this.sortByProp.bind(this, 'lastNewConfirmed');
        this.sortByPeakNewConfirmed = this.sortByProp.bind(this, 'peakNewConfirmed');
        this.sortByConfirmedPercent = this.sortByProp.bind(this, 'lastConfirmedPercent');
        this.sortByPeakActive = this.sortByProp.bind(this, 'peakActive');
        this.sortByPeakActivePercent = this.sortByProp.bind(this, 'peakActivePercent');
        this.sortByPeakConfirmed = this.sortByProp.bind(this, 'peakConfirmed');
        this.sortByPeakConfirmedPercent = this.sortByProp.bind(this, 'peakConfirmedPercent');
        this.sortByDeaths = this.sortByProp.bind(this, 'lastDeaths');
        this.sortByNewDeaths = this.sortByProp.bind(this, 'lastNewDeaths');
        this.sortByPeakNewDeaths = this.sortByProp.bind(this, 'peakNewDeaths');
        this.sortByMortality = this.sortByProp.bind(this, 'lastMortality');
        this.sortByDeathsPercent = this.sortByProp.bind(this, 'lastDeathsPercent');
        this.sortByPopulation = this.sortByProp.bind(this, 'population');
        this.sortByPopulationDensity = this.sortByProp.bind(this, 'populationDensity');
        this.sortByRecoveries = this.sortByProp.bind(this, 'lastRecoveries');
        this.sortByRecoveriesPercent = this.sortByProp.bind(this, 'lastRecoveriesPercent');
        this.sortByPeakDeaths = this.sortByProp.bind(this, 'peakDeaths');
        this.sortByPeakMortality = this.sortByProp.bind(this, 'peakMortality');
        this.sortByPeakDeathsPercent = this.sortByProp.bind(this, 'peakDeathsPercent');
        this.sortByPeakPopulation = this.sortByProp.bind(this, 'population');
        this.sortByPeakRecoveries = this.sortByProp.bind(this, 'peakRecoveries');
        this.sortByPeakRecoveriesPercent = this.sortByProp.bind(this, 'peakRecoveriesPercent');
    }
    static create() {
        return new TimeSeriesArray();
    }
    sortByProp(prop, asc = true) {
        const ret = asc ? -1 : 1;
        this.sort((a, b) => {
            if (a[prop]() < b[prop]()) {
                return ret;
            }
            if (a[prop]() > b[prop]()) {
                return ret * -1;
            }
            return 0;
        });
    }
    sortByCountry(asc = true) {
        this.sort((a, b) => {
            if (a.country() < b.country()) {
                return -1;
            }
            if (a.country() > b.country()) {
                return 1;
            }
            if (a.state() < b.state()) {
                return -1;
            }
            if (a.state() > b.state()) {
                return 1;
            }
            return 0;
        });
    }
}
