import { cloneShallow } from '../../utility';
import { ReadOnlyAble } from '../read-only';
export function createTimeSeriesCount(confirmed = 0, deaths = 0, recoveries = 0) {
    return {
        active: 0,
        confirmed,
        deaths,
        newConfirmed: 0,
        newDeaths: 0,
        recoveries,
        projectionReverseDeath: 0,
    };
}
export class TimeSeries extends ReadOnlyAble {
    constructor(lsData) {
        super();
        this.lsData = lsData;
        this.lastActive = this.lastValue.bind(this, 'active');
        this.lastActivePercent = this.lastPercent.bind(this, 'active');
        this.lastConfirmed = this.lastValue.bind(this, 'confirmed');
        this.lastConfirmedPercent = this.lastPercent.bind(this, 'confirmed');
        this.lastDeaths = this.lastValue.bind(this, 'deaths');
        this.lastDeathsPercent = this.lastPercent.bind(this, 'deaths');
        this.lastRecoveries = this.lastValue.bind(this, 'recoveries');
        this.lastRecoveriesPercent = this.lastPercent.bind(this, 'recoveries');
        this.lastNewConfirmed = this.lastValue.bind(this, 'newConfirmed');
        this.lastNewDeaths = this.lastValue.bind(this, 'newDeaths');
        this.peakActive = this.peakValue.bind(this, 'active');
        this.peakActivePercent = this.peakPercent.bind(this, 'active');
        this.peakConfirmed = this.peakValue.bind(this, 'confirmed');
        this.peakConfirmedPercent = this.peakPercent.bind(this, 'confirmed');
        this.peakDeaths = this.peakValue.bind(this, 'deaths');
        this.peakDeathsPercent = this.peakPercent.bind(this, 'deaths');
        this.peakRecoveries = this.peakValue.bind(this, 'recoveries');
        this.peakRecoveriesPercent = this.peakPercent.bind(this, 'recoveries');
        this.peakNewConfirmed = this.peakValue.bind(this, 'newConfirmed');
        this.peakNewDeaths = this.peakValue.bind(this, 'newDeaths');
        this.counts = this.getProp.bind(this, 'counts');
        this.dates = this.getProp.bind(this, 'dates');
        this.country = this.getProp.bind(this, 'country');
        this.countryCode = this.getProp.bind(this, 'countryCode');
        this.key = this.getProp.bind(this, 'key');
        this.population = this.getProp.bind(this, 'population');
        this.populationDensity = this.getProp.bind(this, 'populationDensity');
        this.state = this.getProp.bind(this, 'state');
        this.stateCode = this.getProp.bind(this, 'stateCode');
    }
    static create(locationSeries) {
        return new TimeSeries(locationSeries);
    }
    lastValue(prop) {
        if (this.lsData.counts.length < 1) {
            return 0;
        }
        return this.lsData.counts[this.lsData.counts.length - 1][prop];
    }
    lastPercent(prop) {
        if (this.lsData.counts.length < 1) {
            return 0;
        }
        if (this.lsData.population < 1) {
            return 0;
        }
        return ((this.lsData.counts[this.lsData.counts.length - 1][prop] /
            this.lsData.population) *
            100);
    }
    peakValue(prop) {
        if (this.lsData.counts.length < 1) {
            return 0;
        }
        return this.lsData.counts.reduce((max, next) => {
            if (next[prop] && next[prop] > max) {
                return next[prop];
            }
            return max;
        }, 0);
    }
    peakPercent(prop) {
        if (this.lsData.counts.length < 1) {
            return 0;
        }
        if (this.lsData.population < 1) {
            return 0;
        }
        return this.lsData.counts.reduce((max, next) => {
            if (next[prop]) {
                const div = (next[prop] / this.lsData.population) * 100;
                if (div > max) {
                    return div;
                }
            }
            return max;
        }, 0);
    }
    getProp(prop) {
        return this.lsData[prop];
    }
    // deep clone of the object, excluding date objects
    clone() {
        return TimeSeries.create(Object.assign(Object.assign({}, this.lsData), { counts: this.lsData.counts.map(cloneShallow), dates: this.lsData.dates.slice(0) }));
    }
    cloneAndAppend(counts, dates) {
        return TimeSeries.create(Object.assign(Object.assign({}, this.lsData), { counts: this.lsData.counts.map(cloneShallow).concat(counts), dates: this.lsData.dates.concat(dates) }));
    }
    lastMortality() {
        if (this.lastConfirmed() < 1) {
            return 0;
        }
        return (this.lastDeaths() / this.lastConfirmed()) * 100;
    }
    peakMortality() {
        if (this.peakConfirmed() < 1) {
            return 0;
        }
        return (this.peakDeaths() / this.peakConfirmed()) * 100;
    }
    formatName() {
        return this.country() + (this.state() ? ', ' + this.state() : '');
    }
    forEachDay(callback) {
        this.counts().forEach((tsc, i) => {
            callback(tsc, this.dates()[i], i);
        });
    }
    forEachDayWhere(predicate, callback) {
        this.forEachDay((t, d, i) => {
            if (predicate(t, d, i)) {
                callback(t, d, i);
            }
        });
    }
}
