function debounce(fn, limit = 50) {
    let isRunning = 0;
    let lastArgs = [];
    const run = () => {
        try {
            fn(...lastArgs);
        }
        catch (e) {
            log('Warning debounced function failed: ' + e.message);
        }
        finally {
            isRunning = 0;
            lastArgs = [];
        }
    };
    return (...args) => {
        lastArgs = args;
        if (isRunning) {
            clearTimeout(isRunning);
            isRunning = setTimeout(run, limit);
        }
        else {
            isRunning = setTimeout(run, limit);
        }
    };
}
