export function buildJsChartingSeries(name) {
    const series = createJsChartingSeriesData(name);
    const seriesBuilder = {
        addPoint(x, y, spreadIn = {}) {
            series.points.push(Object.assign({ x,
                y }, spreadIn));
            return seriesBuilder;
        },
        colour(colour) {
            this.setProp('color', colour);
            return seriesBuilder;
        },
        lineColour(colour) {
            this.setProp('line', { color: colour });
            return seriesBuilder;
        },
        setProp(prop, value) {
            series[prop] = value;
            return seriesBuilder;
        },
        type(type) {
            series.type = type;
            return seriesBuilder;
        },
        collect() {
            return series;
        },
    };
    return seriesBuilder;
}
function createJsChartingSeriesData(name) {
    return {
        name,
        points: [],
    };
}
