import { backslash, comma, doubleQuote, newLine } from '../../constants';
export class Csv {
    constructor(hasHeader, string) {
        this.hasHeader = hasHeader;
        this.string = string;
        this.headerStrings = [];
        this.newLine = newLine;
        this.parsedData = [];
    }
    static rowStringToArray(csvRow) {
        const chars = csvRow.split('');
        const state = {
            buffer: '',
            isEscape: false,
            isInQuote: false,
        };
        const toggleQuoteState = () => {
            if (state.isInQuote) {
                state.isInQuote = false;
            }
            else {
                state.isInQuote = true;
            }
        };
        const toggleEscape = () => {
            if (state.isEscape) {
                state.isEscape = false;
            }
            else {
                state.isEscape = true;
            }
        };
        return chars.reduce((arr, el, i, c) => {
            if (el === doubleQuote) {
                if (state.isEscape) {
                    state.buffer += el;
                    toggleEscape();
                }
                else {
                    toggleQuoteState();
                }
                return arr;
            }
            if (el === backslash) {
                if (state.isEscape) {
                    state.buffer += el;
                    toggleEscape();
                }
                else {
                    toggleEscape();
                }
                return arr;
            }
            if (el === comma) {
                if (state.isInQuote) {
                    state.buffer += el;
                }
                else {
                    arr.push(state.buffer);
                    state.buffer = '';
                }
                return arr;
            }
            state.buffer += el;
            if (i === c.length - 1) {
                arr.push(state.buffer);
                state.buffer = '';
            }
            return arr;
        }, []);
    }
    parse() {
        const splitString = this.string.split(this.newLine);
        this.headerStrings = this.hasHeader
            ? Csv.rowStringToArray(splitString[0])
            : this.headerStrings;
        this.parsedData = (this.hasHeader ? splitString.slice(1) : splitString)
            .map(Csv.rowStringToArray)
            .filter(row => row.length);
        return this.parsedData;
    }
    parsed() {
        if (this.parsedData.length === 0) {
            this.parse();
        }
        return this.parsedData;
    }
    toString() {
        return this.string;
    }
}
