import { h } from 'preact';
import { InputNumber } from './input/number';
import { width2em, styles, } from '../style';
export function InputSeir({ onChange, state, strings }) {
    const { seirInput } = strings;
    const gt0 = (n) => (n < 0 ? 0 : n);
    const percent = (n) => (n < 0 ? 0 : n > 1 ? 1 : n);
    const data = [
        [
            {
                prop: 'r0',
                filter: (n) => (n >= 0 && n < 30 ? n : 2.2),
                unit: '',
                decimals: true,
            },
            {
                prop: 'incubationPeriod',
                filter: gt0,
                unit: seirInput.unitDays,
                decimals: false,
            },
            { prop: 'fatalityRate', filter: percent, unit: '%', decimals: true },
            {
                prop: 'lengthOfSevereHospitalStay',
                filter: gt0,
                unit: seirInput.unitDays,
                decimals: false,
            },
            {
                prop: 'hospitalizationRate',
                filter: percent,
                unit: '%',
                decimals: true,
            },
        ],
        [
            { prop: 'daysToProject', filter: gt0, unit: '', decimals: false },
            {
                prop: 'durationOfInfection',
                filter: gt0,
                unit: seirInput.unitDays,
                decimals: false,
            },
            {
                prop: 'timeFromIncubationToDeath',
                filter: gt0,
                unit: seirInput.unitDays,
                decimals: false,
            },
            {
                prop: 'recoveryTimeForMildCases',
                filter: gt0,
                unit: seirInput.unitDays,
                decimals: false,
            },
            {
                prop: 'timeFromIncubationToHospital',
                filter: gt0,
                unit: seirInput.unitDays,
                decimals: false,
            },
        ],
    ];
    return (h("table", null, data.map(row => (h("tbody", null, [undefined, undefined].map((_, i) => (h("tr", null, row.map(cell => {
        if (cell.prop === '') {
            return h("td", null);
        }
        return i === 0 ? (h("th", null, seirInput[cell.prop])) : (h("td", { style: "text-align: center" },
            h(InputNumber, { classes: styles.input.concat([width2em]), value: state[cell.prop], onChange: (n) => {
                    onChange(Object.assign(Object.assign({}, state), { [cell.prop]: cell.filter(n) }));
                }, placeholder: seirInput[cell.prop], useDecimals: cell.decimals }),
            "\u00A0",
            cell.unit));
    })))))))));
}
